@import '../../variables';

.ways {
  padding: $container-padding;
  color: #6b6b6b;
  margin-bottom: 20px;
  font-family: 'Nunito', sans-serif;

  &__link {
    color: #6b6b6b;
    text-decoration: none;
  }
}

.pizzaPage {
  display: flex;
  align-items: center;
  padding: $container-padding;
  font-family: 'Nunito', sans-serif;
  gap: 50px;

  &__image {
    width: 500px;
    height: 500px;
  }
}

.infoBlock {
  display: flex;
  flex-direction: column;
  height: 500px;
  gap: 15px;
  padding-top: 30px;
  padding-bottom: 30px;

  &__additives {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__describe {
    display: flex;
    flex-direction: column;
    gap: 25px;
    justify-content: center;
  }

  &__title {
    font-size: 40px;
  }

  &__weight {
    color: #797572;
    font-size: 20px;
  }

  &__ingredients {
    font-size: 20px;
    color: #94864e;
  }
}

.priceBlock {
  display: flex;
  align-items: center;
  gap: 50px;
  padding-top: 20px;

  &__price {
    font-size: 30px;
    font-weight: 500;
  }
}

.counter {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 200px;
  padding: 10px;
  border-radius: 30px;
  background-color: #e9e8e7;

  &__icon {
    width: 40px;
    cursor: pointer;
  }

  &__value {
    font-size: 20px;
  }
}

.cart {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 300px;
  gap: 10px;
  border: 2px solid $main-color;
  border-radius: 10px;
  padding: 10px;
  color: $main-color;
  font-weight: bold;
  transition: .5s;
  cursor: pointer;

  &:hover {
    .cart__image, .cart__title {
      filter: brightness(0) invert(1);
    }
    background-color: $main-color;
    transition: .5s;
  }

  &__title {
    font-size: 20px;
  }
}

.cartActive {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 300px;
  gap: 10px;
  background-color: $main-color;
  border-radius: 10px;
  border: 2px solid $main-color;
  padding: 10px;
  color: #fff;
  font-weight: bold;
  transition: .5s;
  cursor: pointer;

  .cart__image, .cart__title {
    filter: brightness(0) invert(1);
  }
}

.checklist {
  &__list {
    list-style-type: none; 
    padding: 0;
    padding-top: 15px;
  }

  &__title {
    font-weight: 500;
    font-size: 30px;
  }

  &__item {
    margin-bottom: 10px;
    position: relative;

    &::before {
      content: '\2022';
      color: #efb729;
      font-size: 20px;
      font-weight: bold;
      position: absolute;
      left: -20px;
      top: 2px;
    }
  }

  &__price {
    float: right;
    font-weight: bold;
    color: #efb729;
  }
}
